import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { Setting } from '../app/app.setting';

/*
  Generated class for the Login provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class HomeService {

  private floorsUrl = this.setting.apiTestHost + '/artradeApp/getfloors.action';

  constructor(public http: Http, private setting: Setting) {
  }

  getFloors(): Observable<any> {
    let options = new RequestOptions({
      headers: new Headers()
    });
    return this.http.get(this.floorsUrl, options)
      .map((res) => {
        let response = res.json();
        if (response.result) {
          let result = response.data.filter((item, index, array) => {
            return item.flShow === 'Y' && (item.showType === '0002' || item.showType === '0003');
          });
          result = result.map((item, index, array) => {
            if (!item.rooms) {
              item.rooms = [];
            } else {
              item.rooms.rmPicUrl = this.setting.apiTestHost + '/gallery' + item.rooms.rmPicUrl;
              item.rooms = [item.rooms];
            }
            return item;
          });
          return result;
        }
        return false;
      })
      .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

}



