import {
  Component, ViewChildren, NgZone, OnInit, QueryList, ViewChild
} from '@angular/core';
import { Item } from '../../models/item';
import { NavController, IonicPage, Content, Slides, ModalController, ToastController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { CategoryService } from '../../service/category';
import { Category } from '../../models/category';
import { HomeService } from '../../service/home';
import { ItemService } from '../../service/item';
import { Artist } from '../../models/artist';
import { Setting } from '../../app/app.setting';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';


@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
  animations: [
    trigger('searchbarState', [
      state('inactive', style({
        'transform': 'translateY(-272px)',
        'opacity': '0'
      })),
      state('active', style({
        'transform': 'translateY(0)',
        'opacity': '1'
      })),
      transition('inactive => active', animate('300ms ease-out')),
      transition('active => inactive', animate('300ms ease-in'))
    ]),
    trigger('toolbarState', [
      state('inactive', style({
        opacity: '1'
      })),
      state('active', style({
        opacity: '0'
      })),
      transition('inactive => active', animate('300ms ease-out')),
      transition('active => inactive', animate('300ms ease-in'))
    ])
  ],
  providers: [CategoryService, ItemService, StatusBar, HomeService]
})
export class HomePage implements OnInit {

  @ViewChild(Content) content: Content;
  @ViewChildren(Slides) slides: QueryList<Slides>;

  searchbarState: string;
  searching: boolean = false;

  searchItemList: Item[] = [];
  search_pnum: number = 1;
  search_pqty: number = 5;

  segment: string;
  errorMessage: string;
  categoryList: Category[] = [];
  categorys: any = [];
  category_pnum: number = 1;//当前页码
  category_pqty: number = 5;//每页数量

  todayItemList: Item[] = [];
  today_pnum: number = 1;//当前页码
  today_pqty: number = 5;//每页数量

  pcateId: number;

  recentItems: Item[] = [];


  types: any = {};
  artist: Artist;

  artistsDisplay: string = '任何艺术家';
  typesDisplay: string = '任何类型';
  timeDisplay: string = '任何时间';

  bannerImgList: string[] = [
    'http://www.artrade.com/images/2017/banner/17042101.jpg',
    'http://www.artrade.com/images/2017/banner/17041401.jpg',
    'http://www.artrade.com/images/2017/banner/17041402.jpg',
    'http://www.artrade.com/images/2017/banner/17041703.jpg'
  ];


  constructor(public navCtrl: NavController, private _ngZone: NgZone, private categoryService: CategoryService, private itemService: ItemService, public modCtrl: ModalController, public toaCtrl: ToastController, private statusBar: StatusBar, private homeService: HomeService, private setting: Setting) {
    this.searchbarState = 'inactive';
    this.segment = 'recommend';
  }

  toggleSearchbar() {
    if (this.searchbarState === 'inactive') {
      this.searchbarState = 'active';
    } else {
      this.searchbarState = 'inactive';
    }
  }

  closeSearchbar() {
    if (this.searchbarState === 'active') {
      this._ngZone.run(() => {
        this.searchbarState = 'inactive';
      });
    }
  }

  enterItem(item: Item) {
    this.navCtrl.push('ItemPage', { item: item });
  }

  clearSearchCondition() {
    this.searching = false;
    this.artistsDisplay = '任何艺术家';
    this.typesDisplay = '任何类型';
    this.timeDisplay = '任何时间';
  }

  doSearching() {
    this.itemService.getSearchItems(this.search_pnum, this.search_pqty, this.artistsDisplay + ',' + this.typesDisplay).subscribe(list => {
      if (list.length > 0) {
        this.searchItemList = list;
        this.searching = true;
        this.search_pnum++;
      }
    });
  }

  getMoreSearchItem() {
    this.itemService.getSearchItems(this.search_pnum, this.search_pqty, this.artistsDisplay + ',' + this.typesDisplay).subscribe(list => {
      if (list.length > 0) {
        this.searchItemList = this.searchItemList.concat(list);
      }
    });
  }

  pickArtist() {
    let artistPicker = this.modCtrl.create('PickArtist', { artist: this.artist });
    artistPicker.onDidDismiss((data) => {
      if (data != undefined) {
        this.artist = data.artist;
        this.artistsDisplay = this.artist.name;
        this.doSearching();
      }
    })
    artistPicker.present();
  }

  pickType() {
    let typePicker = this.modCtrl.create('PickType', { types: this.types });
    typePicker.onDidDismiss((data) => {
      if (data != undefined) {
        this.types = data.types;
        this.typesDisplay = this.types.filter((item) => {
          return item.selected;
        }).map((item) => {
          return item.name;
        }).join('/');
        this.doSearching();
      }
    })
    typePicker.present();
  }

  pickDate() {
    let datePicker = this.modCtrl.create('PickDate');
    datePicker.onDidDismiss(data => {
      if (data != undefined) {
        this.timeDisplay = data.date;
      }
    });
    datePicker.present();
  }

  ngOnInit() {
    this.getFloors();
  }

  getFloors() {
    this.homeService.getFloors().subscribe(result => {
      if (result) {

        this.categorys = result;
      } else {
        this.toaCtrl.create({
          message: '服务器丢了o(╯□╰)o 请稍后再试',
          duration: 1000,
          position: 'middle',
          //showCloseButton: true,
          dismissOnPageChange: true
        }).present();
      }
    })
  }

  enterRoom(room) {
    if (room.rmType === '0002') {
      this.enterCategoryPage(room);
    }
  }
  getCategory() {//获取首页专场列表及相应拍品列表
    this.categoryService.getCategoryListByChannel(1, 1, 5).subscribe(category => {
      this.categorys.zgsh = category.slice(0, 4);
    });
    this.categoryService.getCategoryListByChannel(4, 1, 5).subscribe(category => {
      this.categorys.gczx = category.slice(0, 4);
    });
    this.categoryService.getCategoryListByChannel(5, 1, 5).subscribe(category => {
      this.categorys.ddgy = category.slice(0, 4);
    });
    this.categoryService.getCategoryListByChannel(3, 1, 5).subscribe(category => {
      this.categorys.yhds = category.slice(0, 4);
    });

  }



  ngAfterViewInit() {
    this.slides.changes.subscribe(item => {
      this.reRenderSlide();
    });
  }

  ionViewWillEnter() {
    this.statusBar.styleLightContent();
  }

  enterCategoryPage(category: any) {
    this.navCtrl.push('CategoryPage', {
      //cateId: category.roomId,
      cateId: 1,
      cateName: category.rmAttText
    });
  }


  enterMessagePage() {
    this.navCtrl.push('MessagePage');
  }

  reRenderSlide() {
    let banner = this.slides.first;
    this.slides.map((item: Slides, index: number, array: Slides[]) => {
      if (item != banner) {
        item.slidesOffsetBefore = 24;
        item.spaceBetween = 12;
        item.slidesPerView = 'auto';
        item.slidesOffsetAfter = 24;
      }
    });
  }



}



